html, body {
    background: rgb(131, 198, 221);
    padding: 0px;
}

.toggled {
    height: 100vh;
    margin: 0px;
}

textarea {
    border: none;
    outline: none;
}

.hljs {
    display: block;
    overflow-x: auto;
    padding: 0.5em;
    background: white;
}

.test {
  max-width: 80vw;
}

#input, #preview {
    outline: none;
    border: 1px solid black;

    -webkit-box-shadow: 0px 0px 21px 3px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 21px 3px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 21px 3px rgba(0,0,0,0.75);

    resize: none; /*remove the resize handle on the bottom right*/
    margin: 20px;
}

#header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: left;
    width: 100%;
    background: rgb(81, 178, 221);
    border-bottom: 1px solid black;
}

#editor, #markdowned {
    -webkit-box-shadow: inset 0px 9px 15px -8px rgba(0,0,0,0.75);
    -moz-box-shadow: inset 0px 9px 15px -8px rgba(0,0,0,0.75);
    box-shadow: inset 0px 9px 15px -8px rgba(0,0,0,0.75);
}


#icon {
    padding-right: 5px;
}

h3 {
    padding-left: 5px;
    line-height: .05;
    font-size: 15px;
}


/* markdown styles */
blockquote {
  border-left: 3px solid #224B4B;
  color: #224B4B;
  padding-left: 5px;
  margin-left: 25px;
}

code {
  background: white;
  padding: 1px 4px 2px 4px;
  font-size: calc(5px + .5vw);
  font-weight: bold;
}

pre {
  background: white;
  padding: 5px 0 5px 5px;
}

h1 {
  border-bottom: 2px solid #224B4B;
}

h2 {
  border-bottom: 1px solid #224B4B;
}

table {
  border-collapse: collapse;
}

td,
th {
  border: 2px solid #224B4B;
  padding-left: 5px;
  padding-right: 5px;
}
img {
  max-width: 90%;
  height: auto;
}